.counter-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    padding: 20px;

    .time {
        font-size: 5rem;
        padding: 20px;
    }

    .btns-container {
        button {
            margin: 10px;
            outline: none;
            border: none;
            color: white;
            padding: 20px;
            font-size: 1rem;
            cursor: pointer;
        }

        .pause-start {
            background-color: rgb(0, 129, 195);
        }

        .reset {
            background-color: rgb(255, 0, 83);
        }
    }
}



